.sideBar{
  grid-area: n;
  background-color: rgba(12, 39, 201, 0.25);
  padding: 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  .nav{
    .item{
      font-size: 18px;

      a{
        color: #e8e8e8;
        text-decoration: none;
        &:hover{
          color: gold;
        }
      }
    }
  }
  .friends{
    display: flex;
    flex-direction: column;
    gap: 20px;
    text-align: center;
    .friendsItems{
      display: flex;
      gap: 10px;
    }
  }
}
